}

body{
  font-family: 'Exo 2', sans-serif;
  background: -webkit-linear-gradient(#0C0F0E,#141717); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(#0C0F0E,#141717); /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
  background: -webkit-gradient(linear,left top, left bottom,from(#0C0F0E),to(#141717));
  background: linear-gradient(#0C0F0E,#141717); /* Standard syntax */
}
